<template>
  <div class="startpage">
    <header class="header">
      <div class="logo">
        <img src="../../assets/logo.png" alt>
      </div>
      <div class="rightBox">
        <!--  -->
        <el-button type="primary" @click="toLogin"></el-button>
        <el-button type="primary" plain @click="toRegister"></el-button>
      </div>
    </header>
    <div class="content">
      <div class="swiperBox">
        <swiper-banner :swiperList="swiperList"></swiper-banner>
      </div>
      <div class="toRight">
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import SwiperBanner from '@/components/SwiperBanner/SwiperBanner'
export default {
  data () {
    return {
      swiperList: [
        {
          id: '0001',
          imgUrl:
            'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20194/c0a2572ce14768edf1705799ecfe662b.jpg_750x200_a4c60c41.jpg'
        },
        {
          id: '0002',
          imgUrl:
            'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20194/c1df8f4df8175aba3464cda3214c92e0.jpg_750x200_951f378d.jpg'
        },
        {
          id: '0003',
          imgUrl:
            'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20194/c524bef5f107227816cb920ae21f3483.jpg_750x200_da72a0a1.jpg'
        }
      ]
    }
  },
  components: {
    SwiperBanner
  },
  created () {

  },
  methods: {
    toLogin () {
      this.$router.push('/login')
    },
    toRegister () {
      this.$router.push('/Register/registerInfo')
    }
  }
}
</script>

<style lang="scss" scoped>
.startpage {
  .header {
    width: 100vw;
    height: 60px;
    background: #fff;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      width: 180px;
      height: 100%;
      img {
        display: block;
        width: 140px;
        margin: auto;
        height: 50px;
        margin-top: 5px;
      }
    }
    .rightBox {
      margin-right: 20px;
      display: flex;
      align-items: center;
    }
  }
  .content {
    // background: red;
    .swiperBox {
      height: calc(100vh - 200px);
      padding: 40px 100px;
    }
    .toRight {
      display: flex;
      justify-content: flex-end;
      margin-right: 40px;
      color: #008cff;
      text-decoration: underline;
      cursor: pointer;
    }
  }
}
</style>
<style>
</style>



